
import Msg from 'vux/src/components/msg/index.vue'

export default {
components:{
Msg
},
data:function(){
return {
buttons:[{
type:'primary',
text:'跳转到登录页面',
link:'/login'
}]
}
},
created:function(){
if(this.$store.state.isLogined==false){
return
}

this.$store.commit('mutationsLogout')

window.localStorage.clear();

this.$router.push('/')
}
}



