
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

export default {
components:{
Group,XTextarea,mavonEditor,XButton,Toast
},
data:function(){
return{
title:'',
content:'',
isShowToast:false,
toastText:'',
toolbars: {
bold: true, // 粗体

header: true, // 标题



superscript: true, // 上角标
subscript: true, // 下角标
quote: true, // 引用


link: true, // 链接
imagelink: true, // 图片链接

table: true, // 表格


htmlcode: true, // 展示html源码

/* 1.3.5 */




/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true 
}

}
},
methods:{

















addtopic:function(){
var app=this
this.$http.post('/topics',{
accesstoken:this.$store.state.accesstoken,
title:this.title,
content:this.content,
tab:'dev'
}).then(function(res){
app.$router.replace('/topiclist/dev')
}).catch(function(err){


app.isShowToast=true;
app.toastText='内容不能为空，或已超出限制'
})
}
}
}



