
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
components:{
Group,XTextarea,mavonEditor,XButton
},
props:['id'],
data:function(){
return{
topic:{
title:'',
content:'',

},
toolbars: {
bold: true, // 粗体

header: true, // 标题



superscript: true, // 上角标
subscript: true, // 下角标
quote: true, // 引用


link: true, // 链接


table: true, // 表格


htmlcode: true, // 展示html源码

/* 1.3.5 */




/* 1.4.2 */
navigation: true, // 导航目录
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true 
}
}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.$http.get('/topic/'+to.params.id).then(function(res){
vm.topic=res.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){
var app=this;
this.$http.get('/topic/'+to.params.id).then(function(res){
app.topic=res.data.data
})
next();

},
methods:{

edittopic:function(){
var app = this;
this.$http.post('/topics/update',{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id,
title:this.topic.title,
content:this.topic.content,
tab:'dev'
}).then(function(res){

app.$router.back();
})
}
},

}



