import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

const store=new Vuex.Store({
    state:{
        topics:[//tab切换
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'dev',desc:'测试'}
        ],
        isShowMoreMenus:false,//显示更多内容
       
        isLogined:false,//登录或未登录
        unLoginedMenus:{
            login:'登录'
        },
        loginedMenus:{
            center:'个人中心',
            create:'发表新话题',
            msg:'消息通知',
            logout:'登出'
        },
        loginname:'',//登录用户名
        accesstoken:'',//登陆的账号
        authorId:''//登陆的id
    },
    getters:{
        // moreMenus://如果是未登录状态，显示登录，如果已登录，显示个人中心、消息通知、登出
        moreMenus(state){
            if(state.isLogined){
                return state.loginedMenus
            }else{
                return state.unLoginedMenus
            }
        }
    },
    mutations:{
        //修改actionsheet的显示与否
        mutationsIsShowMoreMenus:function(state,payload){
            state.isShowMoreMenus=payload;
        },
        mutationsIsLogined:function(state){
            state.isLogined=true
        },
        //修改登录名
        mutationsLoginname:function(state,payload){
            state.loginname=payload
        },
        //修改登陆accesstoken账号
        mutationsAccesstoken:function(state,payload){
            state.accesstoken=payload
        },
        //修改登陆的账号id
        mutationsAuthorId:function(state,payload){
            state.authorId=payload
        },
        //登出
        mutationsLogout:function(state){
            state.isLogined=false;
            state.loginname=''
            state.accesstoken=''
            state.authorId=''
        }
    },
    actions:{}
})
export default store;


