
import {mapMutations} from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components:{
Group,XInput, XButton,Toast
},
data:function(){
return{
accesstoken:'',
toastText:'',
isShowToast:false 
}
},
methods:{
...mapMutations(['mutationsIsLogined','mutationsLoginname','mutationsAccesstoken','mutationsAuthorId']),
dologin(){
var that=this

this.$http.post('/accesstoken',{
accesstoken:this.accesstoken
}).then(function(res){

if(res.data.success){

that.mutationsIsLogined();
that.mutationsLoginname(res.data.loginname);
that.mutationsAccesstoken(res.data.accesstoken);
that.mutationsAuthorId(res.data.id)

that.$router.push('/user/'+res.data.loginname)


window.localStorage.setItem('loginStatus',true)
window.localStorage.setItem('loginname',res.data.loginname)
window.localStorage.setItem('accesstoken',that.accesstoken)
window.localStorage.setItem('author_id',res.data.id)
}
}).catch(function(err){


that.isShowToast=true;
that.toastText='错误的accesstoken'
})

}
}
}



